.MenuContainer{
    padding-top: 0.8em;
    width:100%;
    text-align:center;
    color:#212121;
    -webkit-box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
            box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);

}
@media (min-width: 769px){
    .MenuContainer{
        width:25%;
        display:-ms-flexbox;
        display:flex;
        -ms-flex-direction:column;
        flex-direction:column;
        min-height: 100vh;
    }
}




